<template>
  <div>
    <div v-if="projects.length > 0">
      <h3 class="w3-text-green animated slideInLeft">
        Total of
        <span class="w3-tag w3-round w3-green">{{
          modedProjectsList.length
        }}</span>
      </h3>

      <div
        v-for="p in paginatedData"
        :key="p.id"
        class="animated zoomIn w3-col l4 m6 w3-container w3-margin-top w3-margin-bottom"
      >
        <div
          class="container w3-card-4 w3-border w3-border-gray"
          @click="
            $router.push({
              name: 'detail',
              params: { id: p.id, category: p.category }
            })
          "
        >
          <img
            :src="p.image"
            :alt="p.name"
            style="width:100%;"
            class="proj-image"
          />
          <div class="content">
            <h4>{{ p.name }}</h4>
            <p class="w3-small">{{ p.date_created }}</p>
          </div>
        </div>
      </div>

      <projects-paginator
        :actualNumber="actualNumber"
        :pageNumber="pageNumber"
      />

      <div
        v-show="modedProjectsList.length == 0"
        class="w3-yellow w3-container w3-center animated zoomIn"
      >
        <h2>Sorry, none found</h2>
      </div>
    </div>

    <div v-else class="w3-center" style="font-size: 100px">
      <i class="fa fa-spinner w3-text-black w3-spin"></i>
    </div>
  </div>
</template>

<script>
import ProjectsPaginator from "@/components/ProjectsPaginator.vue";
export default {
  name: "ProjectsList",
  components: { ProjectsPaginator },
  data() {
    return {
      size: 12
    };
  },
  computed: {
    actualNumber() {
      return this.modedProjectsList.length;
    },
    pageNumber() {
      var n = 0;
      if (this.$route.query.page) {
        n = Number(this.$route.query.page);
      }
      return n;
    },
    pageCount() {
      let l = this.modedProjectsList.length,
        s = this.size;
      return Math.ceil(l / s);
    },
    projects() {
      return this.$store.state.projects;
    },
    modedProjectsList() {
      var r = this.$route;
      var projects;
      if (r.query.q) {
        projects = this.projects.filter(function(p) {
          return p.name.toLowerCase().includes(r.query.q.toLowerCase());
        });
      } else if (r.params) {
        if (r.params.category) {
          projects = this.projects.filter(function(p) {
            return p.category == r.params.category;
          });
        } else {
          projects = this.projects;
        }
      }
      return projects;
    },
    paginatedData() {
      const start = this.pageNumber * this.size,
        end = start + this.size;
      return this.modedProjectsList.slice(start, end);
    }
  }
};
</script>

