<template>
  <div class="w3-padding-bottom w3-margin-bottom">
    <div v-if="project" class="w3-row">
      <h1 class="w3-col s12 w3-center animated slideInDown">
        {{ project.name }}
      </h1>

      <div class="w3-container w3-center w3-col l6">
        <img :src="project.image" class="w3-card-4 w3-image animated zoomIn" />
      </div>

      <div
        class="animated slideInLeft w3-col l6 w3-container w3-margin-top w3-white w3-border-gray w3-leftbar w3-round"
      >
        <dl>
          <dt class="w3-large">Date created:</dt>
          <dd class="w3-margin-bottom">{{ project.date_created }}</dd>
          <dt class="w3-large">Programming language/s:</dt>
          <dd class="w3-margin-bottom">{{ project.language_used }}</dd>
          <dt class="w3-large">Type:</dt>
          <dd class="w3-margin-bottom">{{ project.type }}</dd>
          <dt class="w3-large">Description:</dt>
          <dd class="w3-margin-bottom">{{ project.description }}</dd>
          <dt class="w3-large">Source code:</dt>
          <dd v-if="project.link">
            You can find the source code
            <a
              :href="project.link"
              target="blank"
              class="w3-text-red w3-hover-blue w3-large"
              >here</a
            >. Good thing he wasn't lazy uploading it.
          </dd>
          <dd v-else>
            Hhmm... Seems that Denniel isn't yet providing the link for its
            source code. Sorry for that.
            <span
              class="w3-border-bottom w3-border-pink w3-text-pink w3-hover-green w3-large"
              style="cursor:pointer"
              title="I'm not a link, just a button."
              @click="$emit('show-contact-modal')"
              >Contact
            </span>
            him if you want to have the source code.
          </dd>
        </dl>
      </div>
    </div>

    <div v-else class="w3-center" style="font-size: 100px">
      <i class="fa fa-spinner w3-text-black w3-spin"></i>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProjectDetail",
  computed: {
    project() {
      var id = this.$route.params.id;
      var project = this.$store.state.projects.filter(function(p) {
        return p.id == id;
      })[0];
      return project;
    },
    meta() {
      var t = {
        title: "Wait...",
        desc: "Wait..."
      };
      if (this.project) {
        t.title = this.project.name;
        t.desc = this.project.description;
      }
      return t;
    }
  },
  metaInfo() {
    return {
      title: this.meta.title,
      meta: [
        { vmid: "description", name: "description", content: this.meta.desc }
      ]
    };
  }
};
</script>
