             w3-green w3-border-gray w3-topbar w3-bottombar"
    >
      <div v-if="$store.state.quote">
        <h3 class="w3-opacity">{{ $store.state.day }} quote</h3>
        <h2>
          <i
            ><i class="fa fa-quote-left"></i>
            <span> {{ $store.state.quote }} </span>
            <i class="fa fa-quote-right"></i
          ></i>
        </h2>
        <h4 class="w3-opacity">- {{ $store.state.person }}</h4>
      </div>

      <div v-else>
        <i class="w3-large fa fa-spinner w3-spin"></i>
      </div>
    </footer>

    <div class="w3-container w3-center w3-margin-top">
      <div>
        <a
          style="color: #3b5998"
          target="blank"
          href="https://www.facebook.com/dennielsadian"
          class="w3-hover-opacity"
          ><i class="fa fa-facebook-square"></i
        ></a>
        <a
          style="color: #1DA1F2"
          target="blank"
          href="https://twitter.com/dennielsadian"
          class="w3-hover-opacity"
          ><i class="fa fa-twitter"></i
        ></a>
        <a
          style="color: #333"
          target="blank"
          href="https://github.com/denniel-sadian"
          class="w3-hover-opacity"
          ><i class="fa fa-github"></i
        ></a>
      </div>

      <form
        id="subscribe-form"
        @submit.prevent="subscribe"
        class="w3-container"
      >
        <input
          type="email"
          id="subscriber-email"
          class="w3-hover-light-gray"
          placeholder="Your email"
          v-model="email"
          required
        /><button type="submit" class="w3-hover-opacity w3-pink">
          <i class="fa fa-bell"></i><b class="w3-hide-small"> Subscribe</b>
