<script>
import axios from "axios";
export default {
  name: "Footer",
  data() {
    return { email: "" };
  },
  methods: {
    subscribe() {
      axios
        .post("https://dsadian.herokuapp.com/blog/api/subscribe/", {
          email: this.email
        })
        .then(function() {
          alert("Thank you!");
        })
        .catch(function() {
          alert("Sorry, subscriber with this email already exists.");
        });
    }
  }
};
</script>

